.header {
  width: 100%;                  
  height: 180px;                  
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-repeat: no-repeat;    
  background-position: center;     
  background-size: contain;        
  background-color: #000;          
}


.header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); }

.header-content {
  position: relative;
  z-index: 1;
  color: white;
}

.header h1 {
  margin: 0;
  font-size: 2.5rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}
